.live {
    display: flex;
    gap: 15px;
    padding-top: 15px;
}
.box {
    border: 1px solid #444;
    padding: 30px;
    border-radius: 24px;
    
}
.box img {
    border-radius: 50%;
    padding: 15px;
}

@media(max-width : 767px){
    .live {
        flex-direction: column;
    }
}